.main-nav {
  position: relative;
  padding: 0 40px 1px 40px;
}

.main-nav__list {
  display: flex;
  padding: 0;
  margin: 0;
  color: $basic-white;
}

.main-nav__item:first-child {
  list-style: none;
}

.main-nav__item a {
  @include font(Gilroy, 500, normal, 18px, 47px, $basic-white );
  margin-right: 27px;
  position: relative;
}

.main-nav__item a:hover,
.main-nav__item a:focus {
  color: $basic-accent;
}

.main-nav__item a:active {
  color: $basic-accent;
  opacity: 0.7;
}

.main-nav__item a:hover::before,
.main-nav__item a:focus::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 1px;
  bottom: -15px;
  left: 0;
  background-color: $basic-accent;
}

@media (max-width: 1239px) {
  .main-nav__item a{
    font-size: 16px;
  }
}

@media (max-width: 1139px) {
  .main-nav {
    padding: 0 30px;
  }

  .main-nav__item a{
    font-size: 14px;
  }
}

@media (max-width: $tablet-max-width) {
  .main-nav {
    display: none;
  }
}
